import { Space } from 'antd'
import {
    EyeOutlined
} from '@ant-design/icons';
import { Link } from 'react-router-dom';

export const interviewCol = ({ handleDelete, limit, page, handleEdit }) => {
    let no = 0 + (page * limit) - limit
    var array = [
        {
            title: '.',
            dataIndex: 'id',
            key: 'id',
            width: 60,
            render: (text, record) => (
                <span>
                    {no += 1}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'idCard',
            key: 'idCard',
            width: 150,
        },
        {
            title: '',
            dataIndex: 'patientId',
            key: 'patientId',
            width: 150,
        },
        {
            title: '',
            dataIndex: 'personalInfos',
            key: 'personalInfos',
            width: 200,
            render: (text, record) => (
                <span size="middle">
                    {
                        record.lastName + " " + record.firstName
                    }
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'gender',
            key: 'gender',
            width: 60,
        },
        {
            title: '',
            dataIndex: 'age',
            key: 'age',
            width: 60,
        },
        {
            title: '',
            dataIndex: 'nationality',
            key: 'nationality',
            width: 120,
        },
        {
            title: '',
            dataIndex: 'address',
            key: 'address',
            width: 250,
            render: (text, record) => (
                <span>
                    {(record.village !== "") && record.village + ", "}
                    {record.commune !== "" && record.commune + ", "}
                    {record.district !== "" && record.district + ", "}
                    {record.province && record.province}
                </span>
            ),
        },
        {
            title: '',
            dataIndex: 'tel',
            key: 'tel',
            width: 150,
        },
        {
            title: '',
            dataIndex: 'other',
            key: 'other',
            width: 150,
        },
        {
            title: '',
            dataIndex: 'interviewed',
            key: 'interviewed',
            width: 150,
            render: (text, record) => {
                let state ="";
                let classN = "badge-purple"
                let c_state = record?.interviewed;

                if(c_state==true){
                    state = ""
                    classN= "badge-success"
                }
                return (
                    <span className={classN}>{state}</span>
                )
            }
        },
        {
            key: 'action',
            dataIndex: 'action',
            fixed: 'right',
            width: 80,
            align: 'center',
            render: (text, record) => (
                <Space size="middle">
                    <Link className="link" to={"/subPeople/" + record.id}><EyeOutlined /></Link>
                </Space>
            ),
        }
    ]
    return array
}